/** –î–æ—Å—Ç–∞—ë—Ç YouTube ID –∏–∑ ID –∏–ª–∏ URL (youtu.be, /embed, /shorts, ?v=) */
function getYouTubeId(urlOrId) {
  if (!urlOrId) return "";
  if (/^[\w-]{11}$/.test(urlOrId)) return urlOrId; // —É–∂–µ ID

  try {
    const u = new URL(urlOrId, location.href);
    if (/youtu\.be$/i.test(u.hostname)) return u.pathname.slice(1);
    const v = u.searchParams.get("v");
    if (v && /^[\w-]{11}$/.test(v)) return v;
    const m = u.pathname.match(/\/(?:embed|v|shorts)\/([^/?#]+)/i);
    if (m && m[1] && /^[\w-]{11}$/.test(m[1])) return m[1];
  } catch {/* ignore */}
  return "";
}

const uniq = (arr) => Array.from(new Set((arr || []).filter(Boolean)));
function shuffle(a) {
  const arr = a.slice();
  for (let i = arr.length - 1; i > 0; i--) {
    const j = (Math.random() * (i + 1)) | 0;
    [arr[i], arr[j]] = [arr[j], arr[i]];
  }
  return arr;
}

