// src/js/artists/features/player-patch.js
// –§–∏–∫—Å—ã: –Ω–∏ –æ–¥–Ω–æ–≥–æ passive-listener —Ç–∞–º, –≥–¥–µ –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è preventDefault,
// + –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–µ –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏–µ –ø—É–∑—ã—Ä—è –±–µ–∑ "–∑–∞–ª–∏–ø–∞–Ω–∏—è".

export default function mountPlayerPatch(player) {
  // –ë–∞–∑–æ–≤—ã–µ —ç–ª–µ–º–µ–Ω—Ç—ã
  const dock = document.querySelector(".am-player");
  if (!dock) return;

  const bubble = document.querySelector(".am-player__bubble");
  const dragzone = document.querySelector(".am-player__dragzone");

  // –†–∞–∑—Ä–µ—à–∞–µ–º –∫–∞—Å—Ç–æ–º–Ω—ã–µ –∂–µ—Å—Ç—ã –±–µ–∑ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
  [bubble, dragzone].forEach((el) => {
    if (el) el.style.touchAction = "none";
  });

  // –ò—Å–ø–æ–ª—å–∑—É–µ–º –Ω–µ-passive —Å–ª—É—à–∞—Ç–µ–ª–∏ —Ç–∞–º, –≥–¥–µ –º–æ–∂–µ–º –≤—ã–∑–≤–∞—Ç—å preventDefault
  const NP = { passive: false };

  // –£–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω–∞—è —Ç–æ—á–∫–∞ —Å—Ç–∞—Ä—Ç–∞ –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏—è: –ª–∏–±–æ —Å–∞–º bubble, –ª–∏–±–æ dragzone
  const handle = bubble || dragzone;
  if (!handle) return;

  let dragging = false;
  let startX = 0, startY = 0;
  let baseLeft = 0, baseTop = 0;

  // –≤—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω–æ: –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã –∏–∑ pointer/touch/mouse —Å–æ–±—ã—Ç–∏—è
  const point = (ev) => {
    if (ev.touches && ev.touches[0]) {
      return { x: ev.touches[0].clientX, y: ev.touches[0].clientY };
    }
    if (ev.changedTouches && ev.changedTouches[0]) {
      return { x: ev.changedTouches[0].clientX, y: ev.changedTouches[0].clientY };
    }
    return { x: ev.clientX, y: ev.clientY };
  };

  const onDown = (ev) => {
    const p = point(ev);
    startX = p.x;
    startY = p.y;

    // –ë—É–¥–µ–º —Ç–∞—Å–∫–∞—Ç—å –∏–º–µ–Ω–Ω–æ –ø—É–∑—ã—Ä—å, –∞ –Ω–µ –≤–µ—Å—å –¥–æ–∫
    const target = bubble || dock;
    const rect = target.getBoundingClientRect();
    baseLeft = rect.left;
    baseTop  = rect.top;

    dragging = true;

    // –ø—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ–º –ø—Ä–æ–∫—Ä—É—Ç–∫—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤–æ –≤—Ä–µ–º—è drag
    if (ev.cancelable) ev.preventDefault();

    window.addEventListener("touchmove", onMove, NP);
    window.addEventListener("mousemove", onMove, NP);
    window.addEventListener("touchend", onUp, NP);
    window.addEventListener("mouseup", onUp, NP);
  };

  const onMove = (ev) => {
    if (!dragging) return;

    const p = point(ev);
    const dx = p.x - startX;
    const dy = p.y - startY;

    const target = bubble || dock;
    const w = target.offsetWidth || 0;
    const h = target.offsetHeight || 0;

    // –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä—É–µ–º –∫–∞–∫ fixed-—ç–ª–µ–º–µ–Ω—Ç (—Ü–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –ø–æ —Ç–æ—á–∫–µ)
    target.style.position = "fixed";
    target.style.left = `${Math.round(baseLeft + dx)}px`;
    target.style.top  = `${Math.round(baseTop  + dy)}px`;
    target.style.right = "auto";
    target.style.bottom = "auto";

    if (ev.cancelable) ev.preventDefault();
  };

  const onUp = (ev) => {
    dragging = false;
    window.removeEventListener("touchmove", onMove, NP);
    window.removeEventListener("mousemove", onMove, NP);
    window.removeEventListener("touchend", onUp, NP);
    window.removeEventListener("mouseup", onUp, NP);
    if (ev.cancelable) ev.preventDefault();
  };

  // –°—Ç–∞–≤–∏–º —Å–ª—É—à–∞—Ç–µ–ª–∏ —Å –ù–ï-passive –æ–ø—Ü–∏–µ–π
  handle.addEventListener("touchstart", onDown, NP);
  handle.addEventListener("mousedown", onDown, NP);

  // –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ: –µ—Å–ª–∏ –ø–ª–µ–µ—Ä —Å–≤—ë—Ä–Ω—É—Ç, –∫–ª–∏–∫–∏/—Å–∫—Ä–æ–ª–ª –¥–æ–ª–∂–Ω—ã –ø—Ä–æ—Ö–æ–¥–∏—Ç—å "—Å–∫–≤–æ–∑—å"
  // —Å–∞–º –¥–æ–∫, –Ω–æ –æ—Å—Ç–∞–≤–∞—Ç—å—Å—è –Ω–∞ –ø—É–∑—ã—Ä–µ.
  dock.classList.add("am-player--patch-applied");
}
